import { useState, useMemo, useCallback, memo } from 'react';
import {
    AreaChart, Area, BarChart, Bar, XAxis, YAxis, CartesianGrid,
    Tooltip, ResponsiveContainer, Legend, ReferenceLine
} from 'recharts';
import {
    DollarSign, TrendingUp, TrendingDown, PiggyBank,
    Calculator, Save, RefreshCw, Download, Plus, Trash2
} from 'lucide-react';
import ChartContainer from '../components/ui/ChartContainer';
import StatCard from '../components/ui/StatCard';
import { formatNumber, formatCurrency, formatPercent } from '../utils/formatters';

/**
 * 현금흐름 분석기 페이지
 * 부동산 투자의 월별/연간 현금흐름 시뮬레이션
 */
const CashflowAnalyzer = memo(() => {
    // 수입 항목
    const [incomes, setIncomes] = useState([
        { id: 1, name: '월세 수입', amount: 150, type: 'monthly' },
        { id: 2, name: '관리비 수익', amount: 10, type: 'monthly' },
    ]);

    // 지출 항목
    const [expenses, setExpenses] = useState([
        { id: 1, name: '대출 이자', amount: 80, type: 'monthly' },
        { id: 2, name: '관리비', amount: 20, type: 'monthly' },
        { id: 3, name: '재산세', amount: 120, type: 'yearly' },
        { id: 4, name: '종합부동산세', amount: 0, type: 'yearly' },
        { id: 5, name: '수선유지비', amount: 50, type: 'yearly' },
        { id: 6, name: '공실 손실', amount: 15, type: 'monthly' },
    ]);

    // 시나리오 설정
    const [scenario, setScenario] = useState('neutral'); // optimistic, neutral, pessimistic
    const [analysisYears, setAnalysisYears] = useState(10);

    // 시나리오별 조정 계수
    const scenarioFactors = {
        optimistic: { income: 1.1, expense: 0.9, growth: 1.03 },
        neutral: { income: 1.0, expense: 1.0, growth: 1.02 },
        pessimistic: { income: 0.85, expense: 1.15, growth: 1.01 },
    };

    // 월간 현금흐름 계산
    const monthlyCashflow = useMemo(() => {
        const factor = scenarioFactors[scenario];

        const totalIncome = incomes.reduce((sum, item) => {
            const amount = item.type === 'monthly' ? item.amount : item.amount / 12;
            return sum + amount * factor.income;
        }, 0);

        const totalExpense = expenses.reduce((sum, item) => {
            const amount = item.type === 'monthly' ? item.amount : item.amount / 12;
            return sum + amount * factor.expense;
        }, 0);

        return {
            income: Math.round(totalIncome),
            expense: Math.round(totalExpense),
            net: Math.round(totalIncome - totalExpense),
        };
    }, [incomes, expenses, scenario]);

    // 연간 현금흐름 데이터 생성
    const yearlyData = useMemo(() => {
        const factor = scenarioFactors[scenario];
        const data = [];
        let cumulativeCashflow = 0;

        for (let year = 1; year <= analysisYears; year++) {
            const growthFactor = Math.pow(factor.growth, year - 1);
            const annualIncome = monthlyCashflow.income * 12 * growthFactor;
            const annualExpense = monthlyCashflow.expense * 12 * growthFactor;
            const annualNet = annualIncome - annualExpense;
            cumulativeCashflow += annualNet;

            data.push({
                year,
                income: Math.round(annualIncome),
                expense: Math.round(annualExpense),
                net: Math.round(annualNet),
                cumulative: Math.round(cumulativeCashflow),
            });
        }

        return data;
    }, [monthlyCashflow, analysisYears, scenario]);

    // 월별 상세 데이터 (1년치)
    const monthlyData = useMemo(() => {
        const data = [];
        const months = ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월'];

        months.forEach((month, index) => {
            // 분기별 세금 반영
            let extraExpense = 0;
            if (index === 5) extraExpense = expenses.find(e => e.name === '재산세')?.amount / 2 || 0;
            if (index === 8) extraExpense = expenses.find(e => e.name === '재산세')?.amount / 2 || 0;
            if (index === 11) extraExpense = (expenses.find(e => e.name === '종합부동산세')?.amount || 0);

            const income = monthlyCashflow.income;
            const expense = monthlyCashflow.expense + extraExpense;

            data.push({
                month,
                income,
                expense,
                net: income - expense,
            });
        });

        return data;
    }, [monthlyCashflow, expenses]);

    // 손익분기점 계산
    const breakEvenYear = useMemo(() => {
        const initialInvestment = 30000; // 초기 투자금 3억 가정
        let cumulative = 0;

        for (let year = 1; year <= 50; year++) {
            const factor = scenarioFactors[scenario];
            const growthFactor = Math.pow(factor.growth, year - 1);
            cumulative += monthlyCashflow.net * 12 * growthFactor;

            if (cumulative >= initialInvestment) {
                return year;
            }
        }
        return null;
    }, [monthlyCashflow, scenario]);

    // 항목 추가
    const addItem = useCallback((type) => {
        if (type === 'income') {
            setIncomes(prev => [...prev, { id: Date.now(), name: '새 수입', amount: 0, type: 'monthly' }]);
        } else {
            setExpenses(prev => [...prev, { id: Date.now(), name: '새 지출', amount: 0, type: 'monthly' }]);
        }
    }, []);

    // 항목 삭제
    const removeItem = useCallback((type, id) => {
        if (type === 'income') {
            setIncomes(prev => prev.filter(item => item.id !== id));
        } else {
            setExpenses(prev => prev.filter(item => item.id !== id));
        }
    }, []);

    // 항목 수정
    const updateItem = useCallback((type, id, field, value) => {
        const setter = type === 'income' ? setIncomes : setExpenses;
        setter(prev => prev.map(item =>
            item.id === id ? { ...item, [field]: field === 'amount' ? Number(value) : value } : item
        ));
    }, []);

    return (
        <div className="page-container">
            {/* Stats */}
            <div className="grid-stats" style={{ marginBottom: 24 }}>
                <StatCard
                    title="월 순수익"
                    value={formatNumber(monthlyCashflow.net)}
                    suffix="만원"
                    subtitle="수입 - 지출"
                    icon={monthlyCashflow.net >= 0 ? TrendingUp : TrendingDown}
                    iconColor={monthlyCashflow.net >= 0 ? '#10b981' : '#ef4444'}
                    iconBg={monthlyCashflow.net >= 0 ? '#d1fae5' : '#fee2e2'}
                />
                <StatCard
                    title="연간 순수익"
                    value={formatNumber(monthlyCashflow.net * 12)}
                    suffix="만원"
                    subtitle="월 순수익 × 12"
                    icon={DollarSign}
                    iconColor="#6366f1"
                    iconBg="var(--color-primary-light)"
                />
                <StatCard
                    title="현금수익률"
                    value={((monthlyCashflow.net * 12) / 30000 * 100).toFixed(1)}
                    suffix="%"
                    subtitle="연간 (투자금 3억 기준)"
                    icon={PiggyBank}
                    iconColor="#f59e0b"
                    iconBg="#fef3c7"
                />
                <StatCard
                    title="손익분기점"
                    value={breakEvenYear || '50+'}
                    suffix="년"
                    subtitle="초기투자금 회수"
                    icon={Calculator}
                    iconColor="#8b5cf6"
                    iconBg="#ede9fe"
                />
            </div>

            <div style={{ display: 'grid', gridTemplateColumns: '350px 1fr', gap: 24 }}>
                {/* Input Panel */}
                <div>
                    {/* Scenario Selection */}
                    <div className="card" style={{ marginBottom: 16 }}>
                        <h3 style={{ fontSize: '0.95rem', fontWeight: 600, marginBottom: 12 }}>시나리오</h3>
                        <div style={{ display: 'flex', gap: 8 }}>
                            {['optimistic', 'neutral', 'pessimistic'].map(s => (
                                <button
                                    key={s}
                                    onClick={() => setScenario(s)}
                                    className={`btn ${scenario === s ? 'btn-primary' : 'btn-ghost'}`}
                                    style={{ flex: 1, fontSize: '0.8rem' }}
                                >
                                    {s === 'optimistic' ? '낙관' : s === 'neutral' ? '중립' : '비관'}
                                </button>
                            ))}
                        </div>
                    </div>

                    {/* Income Items */}
                    <div className="card" style={{ marginBottom: 16 }}>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                            <h3 style={{ fontSize: '0.95rem', fontWeight: 600, color: 'var(--color-success)' }}>
                                수입 항목
                            </h3>
                            <button className="btn btn-ghost btn-icon" onClick={() => addItem('income')}>
                                <Plus size={16} />
                            </button>
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'column', gap: 8 }}>
                            {incomes.map(item => (
                                <div key={item.id} style={{ display: 'flex', gap: 8, alignItems: 'center' }}>
                                    <input
                                        type="text"
                                        value={item.name}
                                        onChange={(e) => updateItem('income', item.id, 'name', e.target.value)}
                                        style={{ flex: 1, fontSize: '0.85rem' }}
                                    />
                                    <input
                                        type="number"
                                        value={item.amount}
                                        onChange={(e) => updateItem('income', item.id, 'amount', e.target.value)}
                                        style={{ width: 80, fontSize: '0.85rem' }}
                                    />
                                    <select
                                        value={item.type}
                                        onChange={(e) => updateItem('income', item.id, 'type', e.target.value)}
                                        style={{ fontSize: '0.8rem', padding: '6px' }}
                                    >
                                        <option value="monthly">월</option>
                                        <option value="yearly">년</option>
                                    </select>
                                    <button
                                        className="btn btn-ghost btn-icon"
                                        onClick={() => removeItem('income', item.id)}
                                        style={{ color: 'var(--color-danger)', padding: 4 }}
                                    >
                                        <Trash2 size={14} />
                                    </button>
                                </div>
                            ))}
                        </div>
                        <div style={{ marginTop: 12, padding: '8px 12px', background: 'var(--color-success-light)', borderRadius: 'var(--radius-sm)', textAlign: 'right' }}>
                            <span style={{ fontSize: '0.8rem', color: 'var(--color-text-secondary)' }}>월 합계: </span>
                            <span style={{ fontWeight: 600, color: 'var(--color-success)' }}>
                                {formatNumber(monthlyCashflow.income)}만원
                            </span>
                        </div>
                    </div>

                    {/* Expense Items */}
                    <div className="card">
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 }}>
                            <h3 style={{ fontSize: '0.95rem', fontWeight: 600, color: 'var(--color-danger)' }}>
                                지출 항목
                            </h3>
                            <button className="btn btn-ghost btn-icon" onClick={() => addItem('expense')}>
                                <Plus size={16} />
                            </button>
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'column', gap: 8 }}>
                            {expenses.map(item => (
                                <div key={item.id} style={{ display: 'flex', gap: 8, alignItems: 'center' }}>
                                    <input
                                        type="text"
                                        value={item.name}
                                        onChange={(e) => updateItem('expense', item.id, 'name', e.target.value)}
                                        style={{ flex: 1, fontSize: '0.85rem' }}
                                    />
                                    <input
                                        type="number"
                                        value={item.amount}
                                        onChange={(e) => updateItem('expense', item.id, 'amount', e.target.value)}
                                        style={{ width: 80, fontSize: '0.85rem' }}
                                    />
                                    <select
                                        value={item.type}
                                        onChange={(e) => updateItem('expense', item.id, 'type', e.target.value)}
                                        style={{ fontSize: '0.8rem', padding: '6px' }}
                                    >
                                        <option value="monthly">월</option>
                                        <option value="yearly">년</option>
                                    </select>
                                    <button
                                        className="btn btn-ghost btn-icon"
                                        onClick={() => removeItem('expense', item.id)}
                                        style={{ color: 'var(--color-danger)', padding: 4 }}
                                    >
                                        <Trash2 size={14} />
                                    </button>
                                </div>
                            ))}
                        </div>
                        <div style={{ marginTop: 12, padding: '8px 12px', background: 'var(--color-danger-light)', borderRadius: 'var(--radius-sm)', textAlign: 'right' }}>
                            <span style={{ fontSize: '0.8rem', color: 'var(--color-text-secondary)' }}>월 합계: </span>
                            <span style={{ fontWeight: 600, color: 'var(--color-danger)' }}>
                                {formatNumber(monthlyCashflow.expense)}만원
                            </span>
                        </div>
                    </div>
                </div>

                {/* Charts */}
                <div style={{ display: 'flex', flexDirection: 'column', gap: 24 }}>
                    {/* Monthly Chart */}
                    <ChartContainer title="월별 현금흐름 (1년)" height={250}>
                        <ResponsiveContainer width="100%" height="100%">
                            <BarChart data={monthlyData}>
                                <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" />
                                <XAxis dataKey="month" tick={{ fontSize: 11 }} />
                                <YAxis tick={{ fontSize: 11 }} tickFormatter={v => `${v}만`} />
                                <Tooltip
                                    contentStyle={{
                                        background: 'var(--color-bg-secondary)',
                                        border: '1px solid var(--color-border)',
                                        borderRadius: 'var(--radius-md)',
                                    }}
                                    formatter={(value) => [`${formatNumber(value)}만원`]}
                                />
                                <Legend />
                                <ReferenceLine y={0} stroke="var(--color-text-tertiary)" />
                                <Bar dataKey="income" name="수입" fill="#10b981" radius={[4, 4, 0, 0]} />
                                <Bar dataKey="expense" name="지출" fill="#ef4444" radius={[4, 4, 0, 0]} />
                            </BarChart>
                        </ResponsiveContainer>
                    </ChartContainer>

                    {/* Yearly Cumulative Chart */}
                    <ChartContainer title="연간 누적 현금흐름" height={300}>
                        <ResponsiveContainer width="100%" height="100%">
                            <AreaChart data={yearlyData}>
                                <CartesianGrid strokeDasharray="3 3" stroke="var(--color-border)" />
                                <XAxis dataKey="year" tick={{ fontSize: 11 }} tickFormatter={v => `${v}년`} />
                                <YAxis tick={{ fontSize: 11 }} tickFormatter={v => `${(v / 10000).toFixed(1)}억`} />
                                <Tooltip
                                    contentStyle={{
                                        background: 'var(--color-bg-secondary)',
                                        border: '1px solid var(--color-border)',
                                        borderRadius: 'var(--radius-md)',
                                    }}
                                    formatter={(value) => [formatCurrency(value)]}
                                />
                                <Legend />
                                <ReferenceLine y={0} stroke="var(--color-text-tertiary)" />
                                <Area
                                    type="monotone"
                                    dataKey="cumulative"
                                    name="누적 현금흐름"
                                    stroke="#6366f1"
                                    fill="#6366f120"
                                />
                                <Area
                                    type="monotone"
                                    dataKey="net"
                                    name="연간 순수익"
                                    stroke="#10b981"
                                    fill="#10b98120"
                                />
                            </AreaChart>
                        </ResponsiveContainer>
                    </ChartContainer>
                </div>
            </div>

            {/* Disclaimer */}
            <div style={{
                marginTop: 24,
                padding: 16,
                background: 'var(--color-bg-tertiary)',
                borderRadius: 'var(--radius-md)',
                fontSize: '0.8rem',
                color: 'var(--color-text-tertiary)',
            }}>
                ⚠️ 본 분석은 참고용이며, 실제 수익과 다를 수 있습니다. 세금 및 비용은 간소화되어 있으며, 정확한 분석은 전문가와 상담하세요.
            </div>
        </div>
    );
});

CashflowAnalyzer.displayName = 'CashflowAnalyzer';

export default CashflowAnalyzer;
